body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#prodmanage {
  height: 1000px;
  width: 1000px;
  background-color: beige;
  border: 1px soild red;
  border-radius: 10px;

}
.bodyclass {
  max-height: 900px;
  max-width: 1000px;
  background-color: gainsboro;
  border-radius: 10px;
  padding: 20px;
  border: 5px solid black;
}
.componentlabel { 
  padding: 20px;
  font-size: 20px;
}

.datacomponent {
  width: 400px;
  height: 40px;
  margin: 20px;
}
.inputcomponent{
  width: 200px;
  padding: 5px;
  border: 2px solid grey;
}

.btn {
  padding: 20px;
  width: 120px;
}
.displayclass {
  display: inline-block;
  height: 300px;
  width: 180px;
  border: 2px solid black;
  padding: 20px;
  margin: 20px;
  
}

.imgclass {
  height: 180px;
  width: 180px;
}

.displaylabel {
  display: block;
  font-family: 'Courier New', Courier, monospace;
  font-size: 20px;
  font-weight: bold;
  padding: 5px;
}

.displaybtnedit {
  background-color: orange;
  padding: 10px 60px 10px 60px;
  display: block;
  width: 180px;

}

.displaybtndelete {
  background-color: red;
  padding: 10px 60px 10px 60px;
  display: block;
  width: 180px;

}

.astyle {
  text-decoration: none;
}
#navheader {
  height: 100px;
  width: 950px;
  padding: 20px 20px 20px 0px;
}

.listyle {
  display: inline-block;
  padding: 20px 40px 20px 10px;
  font-size: 30px;
  border-right: 2px solid black;
}
#linkstyle {
  padding: 0px;
}

